import { useState, useEffect, useContext, useRef } from "react";
import { PageContext } from "../../../context/context.js";
import {
  addCookie,
  getCookie,
  clearCookie,
  URISearch,
  registerHost,
  checkUUID,
  checkEnv,
  shareHandle,
  loopMapMove,
  stopMapMove,
  dateFormStr,
} from "../../../utils/common.js";
import MapObj from "../../../utils/mapData.js";
import {
  getMapData,
  loadInfo,
  loadInfoAgain,
  activityConfig,
  drawPrize,
  drawBigPrize,
} from "../../../utils/service/api.js";
import task_title from "../../../assets/images/task_title.png";
import end_img from "../../../assets/images/end_img.png";
function PlayMain({ refresh, onConditionChange }) {
  const { page, map, role } = useContext(PageContext);
  const [pageData, setPageData] = page;
  const [mapData, setMapData] = map;
  const [roleData, dispatch, rolReducer] = role;
  console.log("playMain", roleData);
  const {
    finalPrizeId,
    allStep,
    taskList,
    roadPrizes,
    Landmarks,
    activeDateStr,
  } = pageData;
  const { vw, vh, tilewidth, tileheight } = mapData;
  const [viewWonPrize, setViewWonPrize] = useState({
    type: "",
    prizeId: "",
    prizeName: "",
    sceneType: "",
    stepCount: "",
    className: "",
    expireDate: "",
  });
  const [prizePostion, setPrizePostion] = useState(""); // scenePrizeShow  场景奖;fristScenePrizeShow 场景奖第一个; generalPrizeShow 普通奖
  const [bigPrizeShow, setBigPrizeShow] = useState(""); // gapOneShow 解锁大奖；gapTwoShow开大奖过场动画；gapTwoAfterShow 开大奖
  const [lotteryStatu, setLotteryStatu] = useState("");
  const [diceClass, setDiceClass] = useState("dice_0");
  const [prizeListShow, setPrizeListShow] = useState(false);
  const [diceAni, setDiceAni] = useState(false);
  const [hasToken, setHasToken] = useState(true);
  const [lotteryNum, setLotteryNum] = useState(0);
  const mapContainerRef = useRef();
  const mapRef = useRef();
  const roleRef = useRef();
  const lotterying = useRef(false);
  const roleRecord = useRef(null);
  const px2rem = (value) => {
    // var docEl = document.documentElement;
    // var baseSize = window.parseFloat(docEl.style.fontSize);
    //// console.log(baseSize)
    return value / 50;
  };
  const markStyle = (obj) => {
    var markWidth = px2rem(tilewidth) + "rem";
    var markHeight = px2rem(tileheight) + "rem";
    var markLeft = px2rem(obj.coordinates[0] * tilewidth) + "rem";
    var markTop = px2rem(obj.coordinates[1] * tileheight) + "rem";
    var styleStr = {
      width: markWidth,
      height: markHeight,
      left: markLeft,
      top: markTop,
    };
    return styleStr;
  };
  const viewCurrentPrize = (item) => {
    // 未获得不可展示
    console.log("item", item);
    if (allStep >= item.stepCount) {
      setViewWonPrize({
        ...item,
      });

      if (viewWonPrize.type == "coupons" || viewWonPrize.type == "coupon") {
        setViewWonPrize({
          ...viewWonPrize,
          expireDate: item.couponsList[0].expireDate,
        });
      }
      setPrizePostion(
        viewWonPrize.sceneType == 1 && viewWonPrize.stepCount == 3
          ? "fristScenePrizeShow"
          : viewWonPrize.sceneType == 1 &&
            viewWonPrize.stepCount > 3 &&
            viewWonPrize.stepCount < 36
          ? "scenePrizeShow"
          : viewWonPrize.sceneType == 2 &&
            viewWonPrize.stepCount > 3 &&
            viewWonPrize.stepCount < 36
          ? "generalPrizeShow"
          : ""
      );
    }
  };
  const viewFinalPrize = () => {
    if (finalPrizeId > 0) {
      var item = MapObj.allPrize.find((aItem) => {
        return aItem.prizeId == finalPrizeId;
      });
      // 奖品弹窗
      viewWonPrize = {
        ...item,
        className:
          item.type == "coupons"
            ? "prize1"
            : item.type == "plus"
            ? "prize2"
            : item.type == "coupon"
            ? "prize3"
            : item.type == "money"
            ? "prize4"
            : "",
      };
      setBigPrizeShow("gapTwoAfterShow");
    }
  };

  const isScrollTop = () => {
    var container = document.querySelector(".viewport");
    var target = document.querySelector(".task_box");
    if (container.scrollTop <= target.offsetTop - vh * 0.5) {
      console.log("fix模块锁定释放");
      onConditionChange(true);
    } else {
      console.log("fix模块锁定");
      onConditionChange(false);
    }
  };
  const stopScroll = () => {
    stopMapMove(mapContainerRef.current);
  };
  // 角度计算
  const ComputeDistance = (x1, y1, x2, y2) => {
    var result = Math.sqrt(Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2));
    return result;
  };
  const computeAngle = function (x1, y1, x2, y2, x3, y3) {
    const diffX = x2 - x1;
    const diffY = y2 - y1;

    // 返回角度,不是弧度
    return (360 * Math.atan(diffY / diffX)) / (2 * Math.PI);
  };
  // 查找下一步位置，返回[列, 行]位置
  const findNextRoute = (current, previous) => {
    // 1. 遍历当前点周围8个点
    // 2. 判断点是否为路线标记（非0）且与 previous不一致 且 距离最近 则返回
    // const [previousCol, previousRow] = previous ? previous.split('-').map(function(i) { Number(i) }) : []
    const previousCol = previous ? Number(previous.split("-")[0]) : undefined;
    const previousRow = previous ? Number(previous.split("-")[1]) : undefined;

    const currentCol = Number(current.split("-")[0]);
    const currentRow = Number(current.split("-")[1]);

    //   console.log('111prev: ', previousCol, previousRow)
    //   console.log('111curr: ', currentCol, currentRow)

    let next = [];
    let distance = ComputeDistance(
      currentCol,
      currentRow,
      currentCol - 1,
      currentRow - 1
    );

    if (
      mapData.tmxData[currentRow - 1] &&
      mapData.tmxData[currentRow - 1][currentCol - 1] &&
      currentRow - 1 !== previousRow &&
      currentCol - 1 !== previousCol &&
      ComputeDistance(currentCol, currentRow, currentCol - 1, currentRow - 1) <
        distance
    ) {
      //   console.log('左上点')
      next = [currentCol - 1, currentRow - 1];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow - 1] &&
      mapData.tmxData[currentRow - 1][currentCol] &&
      currentRow - 1 !== previousRow &&
      ComputeDistance(currentCol, currentRow, currentCol, currentRow - 1) <
        distance
    ) {
      //   console.log('上方点')
      next = [currentCol, currentRow - 1];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow - 1] &&
      mapData.tmxData[currentRow - 1][currentCol + 1] &&
      currentRow - 1 !== previousRow &&
      currentCol + 1 !== previousCol &&
      ComputeDistance(currentCol, currentRow, currentCol + 1, currentRow - 1) <
        distance
    ) {
      //   console.log('右上点')
      next = [currentCol + 1, currentRow - 1];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow] &&
      mapData.tmxData[currentRow][currentCol + 1] &&
      currentCol + 1 !== previousCol &&
      ComputeDistance(currentCol, currentRow, currentCol + 1, currentRow) <
        distance
    ) {
      //   console.log('右方点')
      next = [currentCol + 1, currentRow];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow + 1] &&
      mapData.tmxData[currentRow + 1][currentCol + 1] &&
      currentRow + 1 !== previousRow &&
      currentCol + 1 !== previousCol &&
      ComputeDistance(currentCol, currentRow, currentCol + 1, currentRow + 1) <
        distance
    ) {
      //   console.log('右下点')
      next = [currentCol + 1, currentRow + 1];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow + 1] &&
      mapData.tmxData[currentRow + 1][currentCol] &&
      currentRow + 1 !== previousRow &&
      ComputeDistance(currentCol, currentRow, currentCol, currentRow + 1) <
        distance
    ) {
      //   console.log('下方点')
      next = [currentCol, currentRow + 1];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow + 1] &&
      mapData.tmxData[currentRow + 1][currentCol - 1] &&
      currentRow + 1 !== previousRow &&
      currentCol - 1 !== previousCol &&
      ComputeDistance(currentCol, currentRow, currentCol - 1, currentRow + 1) <
        distance
    ) {
      //   console.log('左下点')
      next = [currentCol - 1, currentRow + 1];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }

    if (
      mapData.tmxData[currentRow] &&
      mapData.tmxData[currentRow][currentCol - 1] &&
      currentCol - 1 !== previousCol &&
      ComputeDistance(currentCol, currentRow, currentCol - 1, currentRow) <
        distance
    ) {
      //   console.log('左方点')
      next = [currentCol - 1, currentRow];
      distance = ComputeDistance(currentCol, currentRow, next[0], next[1]);
    }
    // console.log('行进中的点', current, previous, next)
    return next;
  };
  // 渲染角色
  const renderRole = (col, row, currentData) => {
    console.log("渲染车位置", col, row);
    console.log("渲染角色 - 读取dom", roleRef);
    roleRef.current.style.width = px2rem(tilewidth) + "rem";
    roleRef.current.style.height = px2rem(tileheight) + "rem";

    let top = Math.round(row * tileheight);
    let left = Math.round(col * tilewidth);

    roleRef.current.style.top = px2rem(top) + "rem";
    roleRef.current.style.left = px2rem(left) + "rem";
    console.log("渲染角色 - 读取角色坐标", currentData);
    const nextColIndex = findNextRoute(
      currentData.current,
      currentData.previous
    )[0];
    const nextRowIndex = findNextRoute(
      currentData.current,
      currentData.previous
    )[1];
    const next = [nextColIndex, nextRowIndex].join("-");
    const loadData = {
      ...currentData,
      next: next,
    };
    // setRoleData(prev => loadData)
    dispatch({
      type: "set",
      payload: {
        ...loadData,
      },
    });
    // 计算角度
    const points = {
      next: loadData.next.split("-").map((i) => Number(i)),
      current: loadData.current.split("-").map((i) => Number(i)),
      previous: loadData.previous
        ? loadData.previous.split("-").map((i) => Number(i))
        : null,
    };

    let angle = 0;

    angle = computeAngle(
      points.current[0],
      points.current[1],
      points.next[0],
      points.next[1]
    );
    console.log("对比点与初步角度", points.current, points.next, angle);
    if (points.next[0] < points.current[0] && angle === 0) {
      angle = angle - 90;
    } else {
      angle = angle + 90;
    }
    setTimeout(function () {
      // console.log('最终角度', angle)
      roleRef.current.className = `role role-active role-deg${angle}`;
    }, 500);
  };
  // 获得初始位置
  const renderRoleLocation = () => {
    console.log("判断车的位置");
    function upCallback(currentData) {
      console.log("currentData", currentData);
      console.log("更新了 roleData之后", roleData);
      const centerRow = Math.round(Number(currentData.current.split("-")[1]));
      const centerCol = Number(currentData.current.split("-")[0]);
      console.log("车的位置", centerRow, centerCol);
      renderRole(centerCol, centerRow, currentData);
      setTimeout(() => {
        setMapCenter(centerCol, centerRow, 0);
      }, 1000);
    }
    let loadData;
    if (pageData.allStep == 1) {
      loadData = {
        ...roleData,
        start: "11-16",
        end: "0-0",
        current: "11-16",
        previous: null,
      };
      // setRoleData({
      //   ...roleData,
      //   start: '11-16',
      //   end: '0-0',
      //   current: '11-16',
      //   previous: null
      // }, () =>{
      //   upCallback(roleData)
      // })
    } else {
      loadData = {
        ...roleData,
        current: MapObj.pathCoordinate[pageData.allStep - 1].join("-"),
        previous: MapObj.pathCoordinate[pageData.allStep - 2].join("-"),
      };
      // setRoleData({
      //   ...roleData,
      //   current: MapObj.pathCoordinate[pageData.allStep - 1].join('-'),
      //   previous: MapObj.pathCoordinate[pageData.allStep - 2].join('-')
      // })
    }
    // setRoleData(prev => loadData)
    dispatch({
      type: "set",
      payload: {
        ...loadData,
      },
    });
    upCallback(loadData);
  };
  const getPointIndex = (col, row) => {
    for (var i = 0; i < MapObj.pathCoordinate.length; i++) {
      if (MapObj.pathCoordinate[i].join("-") === `${col}-${row}`) {
        console.log("点下标", i);
        return i;
      }
    }
    return "";
  };
  // 设置地图中心
  const setMapCenter = (col, row, moveTime) => {
    console.log("计算地图中心");
    const scrollTopList = [
      220, 369, 349, 366, 383, 363, 319, 322, 301, 302, 266, 250, 237, 218, 201,
      185, 165, 145, 123, 107, 89, 101, 123, 101, 111, 101, 81, 66, 43, 35, 15,
      0, 0, 0, 0, 0,
    ];
    let target =
      ((scrollTopList[getPointIndex(col, row)] + 667 * 0.5) * vw) / 375 -
      vh * 0.5;
    if (col == 11 && row == 16) {
      target = ((scrollTopList[getPointIndex(col, row)] + 667) * vw) / 375 - vh;
    }
    const mapContainer = mapContainerRef.current;
    const current = mapContainer.scrollTop;
    const diff = current - target;
    const duration = moveTime === 0 ? 0 : moveTime || 1000;
    console.log("mapContainer", mapContainer, current, target, diff);
    if (diff) {
      if (duration === 0) {
        loopMapMove({ dom: mapContainer }, "scrollTop", target, duration);
      } else {
        setTimeout(function () {
          // console.log('地图聚焦小车位置', mapContainer, target, duration)
          loopMapMove({ dom: mapContainer }, "scrollTop", target, duration);
        }, 1000);
      }
    }
  };

  const matchMethod = (item) => {
    // if (this.checkToken()) {
    //   if (item.code == 2) {
    //     // 分享
    //     this.toShare()
    //   } else if (item.code == 3) {
    //     // 打车
    //     this.toByCar()
    //   } else if (item.code == 4) {
    //     // 免密支付
    //     this.payNoCode()
    //   } else if (item.code == 5) {
    //     // 实名认证
    //     this.renZhengType = true
    //   }
    // } else {
    //   this.checkLogin()
    // }
  };
  const beforeDropSice = () => {
    // if (this.lotteryStatu == 2) {
    // // 去登录
    // this.checkLogin()
    // return
    // }
    // if (this.lotteryStatu == 3) {
    // // 掷骰子
    // this.dropDice()
    // return
    // }
    // if (this.lotteryStatu == 4) {
    // // 加次数任务
    // this.toggleTask()
    // return
    // }
    // if (this.lotteryStatu == 5) {
    // // 开大奖
    // this.endLottery()
    // return
    // }
    // if (this.lotteryStatu == 6) {
    // // 去打车
    // this.toByCar()
    // return
    // }
    // dropDice();
    setLotteryNum(prev => prev + 1)
  };
  const viewMyPrize = () => {
    setPrizeListShow(true);
  };
  const toggleTask = () => {
    var target = document.querySelector(".task_box").offsetTop - 210;
    mapContainerRef.current.scrollTop = target;
  };
  
  

  useEffect(() => {
    renderRoleLocation();
  }, []);

  useEffect(() => {
    let lotteryStatu = "";
    if (pageData.inAvtive === false) lotteryStatu = 1; // 活动不在有效期
    if (!hasToken) return 2; // 未登录
    if (!pageData.finalPrizeLock && pageData.remainCount > 0) lotteryStatu = 3; // 未解锁大奖，有剩余次数
    if (!pageData.finalPrizeLock && pageData.remainCount == 0) lotteryStatu = 4; // 未解锁大奖，没有剩余次数
    if (pageData.finalPrizeLock && pageData.finalPrizeId == 0) lotteryStatu = 5; // 解锁大奖，未开奖
    if (pageData.finalPrizeLock && pageData.finalPrizeId > 0) lotteryStatu = 6; // 解锁大奖，已开奖
    setLotteryStatu(lotteryStatu);
  }, [pageData]);
  useEffect(() => {
    setDiceClass(pageData.resultNum);
  }, [pageData]);

  useEffect(() => {
    // 掷骰子获取可移动步数
    const dropDice = async () => {
      const params = {
        sso_tk: 1,
        inviteToken: '',
      };
      if (params.inviteToken == "undefined" || !params.inviteToken) {
        params.inviteToken = "";
      }
      const res = await drawPrize(params).catch(() => {
        // self.$toast("网络异常，请稍后再试");
      })
      if (res.code == 0) {
        roleRecord.current = {
          ...roleData
        }
        const sourceData = res.data.prizeInfo;
        let resultNum
        // 第一次掷骰子校准
        if (sourceData.stepCount == 3) {
          resultNum = 2;
        } else {
          resultNum = res.data.step;
        }
        setPageData(prev =>{
          return {
            ...prev,
            resultNum: resultNum
          }
        })
        // 焦点转移到车的位置
        const currentColIndex = roleRecord.current.current.split("-")[0];
        const currentRowIndex = roleRecord.current.current.split("-")[1];
        setMapCenter(currentColIndex, currentRowIndex, 0);
        setTimeout(function () {
          // 停止骰子动画
          setDiceAni(prev => false)
          // 小车移动停止后回调
          var callbackFn = function () {
            let prizeInfo = viewWonPrize
            if (sourceData.stepCount < 36) {
              // 奖品弹窗
              prizeInfo = {
                ...prizeInfo,
                ...sourceData,
                ...MapObj.allPrize.find((aItem) => {
                  return aItem.prizeId == sourceData.prizeId;
                })
              }
              
              if (prizeInfo.type == "coupon") {
                prizeInfo.expireDate = sourceData.couponsList[0].expireDate;
              }
              setViewWonPrize(prev => prizeInfo);
            }
            // 根据奖品信息展示弹窗
            setTimeout(function () {
              if (
                prizeInfo.sceneType == 1 &&
                sourceData.stepCount == 3
              ) {
                // 第一个场景奖
                setPrizePostion(prev => 'fristScenePrizeShow')
              } else if (
                prizeInfo.sceneType == 1 &&
                sourceData.stepCount > 3 &&
                sourceData.stepCount < 36
              ) {
                // 其它场景奖
                setPrizePostion(prev => 'scenePrizeShow')
              } else if (
                prizeInfo.sceneType == 2 &&
                sourceData.stepCount > 3 &&
                sourceData.stepCount < 36
              ) {
                // 普通奖
                setPrizePostion(prev => 'generalPrizeShow')
              } else if (sourceData.stepCount == 36) {
                // 解锁大奖
                setBigPrizeShow(prev => 'gapOneShow')
              }
              lotterying.current = false;
              refresh()
            }, 1000);
          };
          // 按指定步数移动小车
          loopRoleMove(resultNum, callbackFn);
          // setTimeout(function () {
          //   self.getAllStep();
          // }, 500);
        }, 1000);
      } else {
        // self.diceAni = false;
        // self.lotterying = false;
        // if (res.code == -2) {
        //   self.currentToken = "";
        //   self.checkLogin();
        // } else {
        //   self.$toast(res.msg);
        // }
      }
    };
    const loopRoleMove = (num, callbackFn) => {
      if (roleRecord.current.current == roleRecord.current.end) {
        setPageData(prev =>{
          return {
            ...prev,
            allStep: 36
          }
        })
        lotterying.current = false;
        return
      }
      var num = num || 0
      move()
      num--
      if (num > 0) {
        setTimeout(() => {
          loopRoleMove(num, callbackFn)
        }, 500)
      } else {
        typeof callbackFn == 'function' && callbackFn()
      }
    }
    // 移动一格
    const move = () => {
      var self = this
      const nextColIndex = findNextRoute(roleRecord.current.current, roleRecord.current.previous)[0]
      const nextRowIndex = findNextRoute(roleRecord.current.current, roleRecord.current.previous)[1]
      // self.roleLocation.next = [nextColIndex, nextRowIndex].join('-')
      // self.roleLocation.previous = self.roleLocation.current
      // self.roleLocation.current = self.roleLocation.next
      // self.roleLocation.next = findNextRoute(self.roleLocation.current, self.roleLocation.previous).join('-')
      roleRecord.current = {
        ...roleRecord.current,
        previous: roleRecord.current.current,
        current: [nextColIndex, nextRowIndex].join('-'),
        next: findNextRoute(roleRecord.current.current, roleRecord.current.previous).join('-')
      }
      dispatch({
        type: "set",
        payload: {
          ...roleRecord.current,
        },
      });
      // 计算角度
      const points = {
        next: roleRecord.current.next.split('-').map((i) => Number(i)),
        current: roleRecord.current.current.split('-').map((i) => Number(i)),
        previous: roleRecord.current.previous.split('-').map((i) => Number(i))
      }
  
      let angle = 0
  
      angle = computeAngle(points.current[0], points.current[1], points.next[0], points.next[1])
  
      if (points.next[0] < points.current[0] && angle === 0) {
        angle = angle - 90
      } else {
        angle = angle + 90
      }
  
      //// console.log('angle: ', angle)
  
      // self.$refs.role.style.transform = `rotate(${angle}deg)`
  
      // self.$refs.role.className = `role role-active`
  
      if (!roleRef.current.classList.contains('role-active')) {
        roleRef.current.classList.add('role-active')
      }
      setTimeout(function () {
        roleRef.current.className = `role role-active role-deg${angle}`
      }, 500)
  
      renderRole(nextColIndex, nextRowIndex, roleRecord.current)
    }
    if (!lotterying.current && lotteryNum > 0) {
      lotterying.current = true
      setDiceAni(true)
      dropDice()
    }
  }, [lotteryNum])

  return (
    <>
      <div
        className={`viewport ${prizeListShow ? "no_scorll" : ""}`}
        ref={mapContainerRef}
        onScroll={isScrollTop}
      >
        <section className="main_area">
          {/* <!-- 高度占位区 --> */}
          <div className="height_area">
            <div
              className={`finally_box ${finalPrizeId != 0 ? "opened" : ""}${
                finalPrizeId == 0 ? "closed" : ""
              }`}
              onClick={viewFinalPrize}
            ></div>
          </div>
          {/* <!-- 大富翁 --> */}
          <div className="canvas" onTouchStart={stopScroll}>
            <div className="sence">
              <div className="map" ref={mapRef}></div>
              <div className="role" ref={roleRef}></div>
              <div className="landmarks">
                {Landmarks.map((item, index) => (
                  <div className="mark_box" style={markStyle(item)}>
                    <div
                      className={`mark_img ${
                        allStep > item.stepCount ? "bag_got" : ""
                      }${allStep < item.stepCount ? "bag_nogot" : ""}`}
                      onClick={viewCurrentPrize.bind(this, item)}
                    ></div>
                    {allStep > item.stepCount && (
                      <div className="mark_text">{item.text}</div>
                    )}
                  </div>
                ))}
                {/* <div className="mark_box" v-for="(item, index) in Landmarks" style={markStyle(item)}>
								<div className={`mark_img ${ allStep > item.stepCount ? 'bag_got' : '' }${ allStep < item.stepCount ? 'bag_nogot' : '' }`} onClick={viewCurrentPrize(item)}></div>
								<div className="mark_text" v-show="allStep > item.stepCount">{item.text}</div>
							</div> */}
                {roadPrizes.map((item, index) => (
                  <div className="prize coupon" style={markStyle(item)}>
                    {allStep > item.stepCount && (
                      <div
                        className="prize_open prize_up"
                        style={{ backgroundImage: "url(" + item.iconUrl + ")" }}
                        onClick={viewCurrentPrize.bind(this, item)}
                      ></div>
                    )}
                  </div>
                ))}
                {/* <div v-for="item in roadPrizes" className="prize coupon" style={markStyle(item)}>
								<div className="prize_open prize_up" style={{ backgroundImage: 'url(' + item.iconUrl + ')' }} v-show="allStep > item.stepCount" onClick={viewCurrentPrize(item)}></div>
							</div> */}
              </div>
            </div>
          </div>
          {/* <!-- 大富翁 end --> */}
          {/* <!-- 加次数任务 --> */}
          <div className="task_box">
            <ul className="task_list">
              {taskList.map((item, index) => (
                <li className="task_list_item">
                  <div className="task_list_icon">
                    <img className="task_list_icon_img" src={item.icon} />
                  </div>
                  <div className="task_list_info">
                    <p>{item.name}</p>
                    <p>{item.desc}</p>
                  </div>
                  <div className="task_list_btns">
                    {item.status == 1 ? (
                      <p className="disabled">{item.unLockText}</p>
                    ) : (
                      <p onClick={matchMethod(item)}>{item.lockText}</p>
                    )}
                  </div>
                </li>
              ))}
              {/* <li className="task_list_item" v-for="item in taskList">
                <div className="task_list_icon">
                  <img className="task_list_icon_img" src={item.icon} />
                </div>
                <div className="task_list_info">
                  <p v-text="item.name"></p>
                  <p v-text="item.desc"></p>
                </div>
                <div className="task_list_btns">
                  <p v-if="item.status == 1" className="disabled">
                    {item.unLockText}
                  </p>
                  <p v-else onClick={matchMethod(item)}>
                    {item.lockText}
                  </p>
                </div>
              </li> */}
            </ul>
            <img className="task_title_img" src={task_title} />
          </div>
          {/* <!-- 加次数任务 end --> */}
          <div className="end_icon_box">
            <img className="end_icon" src={end_img} />
            <div style={{ color: "#758bf0" }}>本活动与苹果公司无关</div>
          </div>
        </section>
        {/* <!-- 摇骰子按钮 --> */}
        <div className="foot_box">
          <div className="dice" onClick={beforeDropSice}>
            {diceAni ? (
              <div className="animate dice_shake"></div>
            ) : (
              <div className={`result_num ${diceClass}`}></div>
            )}
          </div>
          <div className="btns">
            <div className="join_btn">
              <div
                className={`btn ${"lottery" + lotteryStatu}`}
                onClick={beforeDropSice}
              ></div>
              {lotteryStatu == 2 || lotteryStatu == 3 ? (
                <div className="rimain_num">
                  {(hasToken ? pageData.remainCount : "?") + "次"}
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="join_guide">
              {lotteryStatu < 5 && <div>前行每一步，均可随机获得专属奖励</div>}
              {lotteryStatu == 5 && (
                <div>恭喜您已到达终点，快快抽取大奖吧！</div>
              )}
              {lotteryStatu == 6 && <div>您已通关，现在打车立享福利哦~</div>}
            </div>
          </div>
          <div className="my_prize" onClick={viewMyPrize}>
            <div className="my_prize_text">我的奖品</div>
          </div>
          <div className="speed_tip" onClick={toggleTask}>
            <div className="speed_tip_text">加速秘籍</div>
          </div>
        </div>
        {/* <!-- 摇骰子按钮 end --> */}
      </div>
    </>
  );
}

export default PlayMain;
