import { useState, useEffect, useContext } from "react";
import { PageContext } from "../../../context/context";
import PlayFixed from "./playFixed";
import PrizeLoop from "./prizeLoop";
import PlayMain from './playMain'
function Play({ fixdata, onConditionChange, refresh }) {
  const { page, map, role } = useContext(PageContext);
  const [pageData, setPageData] = page
  const [mapData, setMapData] = map
  const [roleData, dispatch, rolReducer] = role
  return (
    <>
      <PlayFixed fixdata={fixdata}></PlayFixed>
      {pageData.prizeMsgList && pageData.prizeMsgList.length > 0 && (
        <PrizeLoop></PrizeLoop>
      )}
      <PlayMain refresh={refresh} onConditionChange={onConditionChange}></PlayMain>
    </>
  );
}

export default Play;
