// 浏览器环境检测
function checkEnv() {
	var ua = window.navigator.userAgent.toLowerCase()
    if (ua.match(/SQYC/i) == 'sqyc' || getCookie('productid') == 'RiderApp') {
        return 'passengeApp'
    } else if (ua.match(/MicroMessenger/i) == 'micromessenger') {
        if (URISearch('isxcx')) {
            return 'miniPro'
        }
        return 'Weixin'
    } else if (ua.match(/AlipayClient/i) == 'alipayclient') {
        var isxcx = URISearch('isxcx') || getCookie('isxcx'),
            clientEntry = URISearch('clientEntry') || getCookie('clientEntry')
        if (isxcx && clientEntry == 2) {
            return 'miniProAli'
        }
        return 'Alipay'
    } else {
        return 'other'
    }
}
// 时间格式
function dateFormStr(temp, type) {
	if (!temp) return ''
	var T = new Date(temp)
	var obj = {
		Y: '',
		M: '',
		D: '',
		h: '',
		m: ''
	}
	obj.Y = T.getFullYear()
	obj.M = T.getMonth() + 1
	obj.D = T.getDate()
	obj.h = T.getHours()
	obj.m = T.getMinutes()
	if (type === 'M-D') return obj.M + '.' + obj.D
	if (type === 'Y-M-D') return obj.Y + '.' + obj.M + '.' + obj.D
	for (var key in obj) {
		if (obj[key] < 10) {
			obj[key] = '0' + obj[key]
		}
	}
	if (type === 'MM-DD') return obj.M + '.' + obj.D
	if (type === 'YY-MM-DD') return obj.Y + '.' + obj.M + '.' + obj.D
	if (type === 'YY-MM-DD hh:mm') return obj.Y + '-' + obj.M + '-' + obj.D + ' ' + obj.h + ':' + obj.m
}
// 版本号对比 newVal >= oldVal
function judgeVersion(newVal, oldVal) {
	if (!newVal || !oldVal) {
		return false
	}
	var versionArrNew = newVal.split('.')
	var versionNumn1 = +versionArrNew[0]
	var versionNumn2 = +versionArrNew[1]
	var versionNumn3 = +versionArrNew[2]
	var versionArrOld = oldVal.split('.')
	var versionNumno1 = +versionArrOld[0]
	var versionNumno2 = +versionArrOld[1]
	var versionNumno3 = +versionArrOld[2]
	if (versionNumn1 > versionNumno1) {
		return true
	} else if (versionNumn1 === versionNumno1) {
		if (versionNumn2 > versionNumno2) {
			return true
		} else if (versionNumn2 === versionNumno2) {
			if (versionNumn3 >= versionNumno3) {
				return true
			}
		}
	}
	return false
}
// 微信环境二次分享
function shareHandle(shareObj, callback) {
	//当前页面url(不可修改)
	var share_thisUrl = window.location.href.split('#')[0]
	//分享title
	var share_title = shareObj.title
	//分享描述
	var share_desc = shareObj.desc
	//分享图标
	var share_imgUrl = shareObj.icon
	//分享后的url（仅限 JS安全接口域名内的地址）
	var share_urlShare = shareObj.htmlUrl
	var SQYC
	if (SQYC) {
		SQYC.request({
			type: 'post',
			url: 'https://m.01zhuanche.com/touch/wxshareMsg/msgn',
			dataType: 'json',
			data: {
				url: share_thisUrl
			},
			credentials: true,
			success: function (res) {
				window.wx.config({
					debug: false,
					appId: res.appId,
					timestamp: res.timestamp,
					nonceStr: res.nonceStr,
					signature: res.signature,
					jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage']
				})
				window.wx.ready(function () {
					// config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
					window.wx.onMenuShareTimeline({
						title: share_title,
						link: share_urlShare,
						imgUrl: share_imgUrl,
						success: function (res) {
							if (callback && typeof callback === 'function') {
								callback(res)
							}
						},
						cancel: function (res) {
							if (callback && typeof callback === 'function') {
								callback(res)
							}
						}
					})
					window.wx.onMenuShareAppMessage({
						title: share_title,
						desc: share_desc,
						link: share_urlShare,
						imgUrl: share_imgUrl,
						trigger: function (res) {
							// 不要尝试在trigger中使用ajax异步请求修改本次分享的内容，因为客户端分享操作是一个同步操作，这时候使用ajax的回包会还没有返回
						},
						success: function (res) {
							if (callback && typeof callback === 'function') {
								callback(res)
							}
						},
						cancel: function (res) {
							if (callback && typeof callback === 'function') {
								callback(res)
							}
						},
						fail: function (res) {
							if (callback && typeof callback === 'function') {
								callback(res)
							}
						}
					})
				})
				window.wx.error(function (res) {})
			},
			error: function (res) {
				//debugger;
				console.log(res.msg)
			}
		})
	} else {
		console.log('需引入公共依赖js，c.static.constant.host.js')
	}
}
/**
 * 唯一身份标识，只做上报数据用
 * localStorage
 */
 function checkUUID() {
	if (window.localStorage) {
		var storage = window.localStorage
        var uuid
		if (typeof storage.SQUUID == 'undefined') {
			var d = new Date().getTime()
			uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
				var r = (d + Math.random() * 16) % 16 | 0
				d = Math.floor(d / 16)
				return (c == 'x' ? r : (r & 0x3) | 0x8).toString(16)
			})
			storage.SQUUID = uuid
		} else {
			uuid = storage.SQUUID
		}
		return uuid
	}
}


function driverPay (funcName, data, callback) {
    var oldName = 'Cb'
    var newName = 'cb'
    if (callback) {
        // 安卓返回参数多个，IOS返回参数为json对象
        // 老回调函数安卓在用，新回调函数IOS在用
        window[funcName + oldName] = window[funcName + newName] = function (res) {
            console.log('返回参数', arguments)
            try {
                // 返回数据为一个参数，json对象 {orderNo, payType, state}
                if (typeof arguments[0] == 'object') {
                    callback(arguments[0].orderNo, arguments[0].payType, arguments[0].state)
                }
                // 返回数据为多个参数，orderNo payType state
                else if (typeof arguments[0] == 'string') {
                    callback(arguments[0], arguments[1], arguments[2])
                } else {
                    console.log('无效返回参数')
                }
                // if (arguments.length == 1) {
                //     callback(arguments[0].orderNo, arguments[0].payType, arguments[0].state)
                // } else if (arguments.length > 1) { 
                //     callback(arguments[0], arguments[1], arguments[2])
                // }
            } catch (err) {
                console.log('回调错误')
            }
            delete window[funcName + oldName]
            delete window[funcName + newName]
        }
    }
    if (window.webkit && window.webkit.messageHandlers) {
        console.log('IOS-参数', data)
        var messageHandlers = window.webkit.messageHandlers
        messageHandlers[funcName].postMessage({
            data: Object.assign({}, data),
            callbackid: funcName + newName,
        })
    } else {
        if (window.MutualAppH5DTO) {
            console.log('调起安卓-参数', data)
            window.MutualAppH5DTO[funcName](data.orderNo, data.amount, data.balance, data.channelId, data.productName)
        }
    }

}

// 加
function addition (a, b) {
    var a = a || 0
    var b = b || 0
    var c, d, e
    try { c = a.toString().split('.')[1].length } catch (error) { c = 0 }
    try { d = b.toString().split('.')[1].length } catch (error) { d = 0 }
    e = Math.pow(10, Math.max(c, d))
    return (multiply(a, e) + multiply(b, e)) / e
}
// 减
function subtraction(a,b){
    var c, d, e
    try { c = a.toString().split(".")[1].length } catch (error) { c = 0 }
    try { d = b.toString().split(".")[1].length } catch (error) { d = 0 }
    return e = Math.pow(10, Math.max(c, d)), (multiply(a, e) - multiply(b, e)) / e
}
// 乘
function multiply (a, b) {
    var a = a || 0
    var b = b || 0
    var c = 0, d, e
    try { d = a.toString() } catch (error) { d = 0 }
    try { e = b.toString() } catch (error) { e = 0 }
    try { c += d.split('.')[1].length } catch (error) {}
    try { c += e.split('.')[1].length } catch (error) {}
    return Number(d.replace('.', '')) * Number(e.replace('.', '')) / Math.pow(10, c)
}
// 除
function division(a, b) {
    var c, d, e = 0, f = 0
    try { e = a.toString().split('.')[1].length } catch (error) {}
    try { f = b.toString().split('.')[1].length } catch (error) {}
    try { c = Number(a.toString().replace('.', '')) }  catch (error) { c = 0 }
    try { d = Number(b.toString().replace('.', '')) }  catch (error) { d = 0 }
    return multiply(c / d, Math.pow(10, f - e))
}

function spellParams ({url = '', params = {}, needRoll = true} = {}) {
    if (JSON.stringify(params) == '{}' || !url) return url
    for (var key in params) {
        url = url + urlExamine(url) + key + '=' + params[key]
    }
    if (needRoll) url = url + urlExamine(url) + 'vt=' + new Date().getTime()
    return url
}

function urlExamine (url = '') {
    return url.indexOf('?') > -1 ? '&' : '?'
}


function getCookie (name){
    var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)");
    arr=document.cookie.match(reg)
    if(arr){
        return decodeURIComponent(arr[2]);
    }else{
        return null;
    }
}

function addCookie (name, value, expires, path, domain, secure) {
    var cookieString = name + '+' + encodeURIComponent(value)
    if (expires) {
        var t = new Date()
        t.setTime(t.getTime() + expires * 60 * 60 * 1000)
        cookieString =+ ';expires=' + t.toUTCString()
    }
    if (path) {
        cookieString += '; path=' + path
    }
    if (domain) {
        cookieString += '; domain=' + domain
    }
    if (secure) {
        cookieString += '; secure'
    }
}

function clearCookie (name) {
    addCookie(name, '', -1)
}

function URISearch (name) {
    var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
    var r = window.location.search.substr(1).match(reg); //获取url中"?"符后的字符串并正则匹配
    var context = '';
    if (r != null)
        context = decodeURIComponent(r[2]);
    reg = null;
    r = null;
    return context == null || context == '' || context == 'undefined' ? "" : context;
}

function registerHost (target, env) {
    var env = env || ''
    var hostEnv = window.location.host
    if (/localhost|(127\.0\.0\.1)/g.test(hostEnv)) {
        if (!env.match(/dev-|test-|pre-/g)) {
            env = 'dev-'
        }
    }
    if (hostEnv.match(/dev-|test-|pre-/g)) {
        env = hostEnv.match(/dev-|test-|pre-/g)[0]
    }
    if (!/(http:\/\/)|(https:\/\/)/g.test(target)) {
        return 'https://' + env + hostEnv
    }
    return  target.replace(/https:\/\//,'https://' + env)
}


// 停止地图移动
function stopMapMove(obj) {
	clearTimeout(obj.timer)

	obj.timer = null
}


function startMove(obj, json, fn) {
	clearInterval(obj.timer)

	obj.timer = setInterval(function () {
		var bFlag = true

		for (var attr in json) {
			var iCur = 0

			if (attr == 'opacity') {
				iCur = Math.round(parseFloat(getStyle(obj, attr)) * 100)
			} else if (attr == 'scroll') {
				iCur = getScroll()
			} else {
				iCur = parseInt(getStyle(obj, attr))
			}

			var iSpeed = (json[attr] - iCur) / 8
			iSpeed = iSpeed > 0 ? Math.ceil(iSpeed) : Math.floor(iSpeed)

			if (attr == 'opacity') {
				obj.style.filter = 'alpha(opacity=' + (iSpeed + iCur) + ')'
				obj.style.opacity = (iSpeed + iCur) / 100
			} else if (attr == 'scroll') {
				obj.scrollTop = obj.scrollTop = iSpeed + iCur
			} else {
				obj.style[attr] = iSpeed + iCur + 'px'
			}

			if (json[attr] != iCur) {
				bFlag = false
			}
		}

		if (bFlag) {
			clearInterval(obj.timer)
			if (fn) {
				fn.call(obj)
			}
		}
	}, 30)
}

// 获得元素属性的值
function getStyle(obj, attr) {
	if (obj.currentStyle) {
		return obj.currentStyle[attr]
	} else {
		return window.getComputedStyle(obj, false)[attr]
	}
}

// function getScroll() {
//     return document.documentElement.scrollTop || document.body.scrollTop;
// }

// 获得滚动轴卷去高度
function getScroll(obj, attr = 'scrollTop') {
	return obj[attr] || document.body[attr]
}

// 移动地图
function loopMapMove(obj, attr, target, duration, fps = 30) {
	//  console.log('聚焦小车位置', obj, attr, target, duration, fps)
	var interval = 1000 / fps
	var isScroll = ['scrollTop']
	let current = 0
	clearTimeout(obj.timer)
	if (duration == 0) {
		if (isScroll.includes(attr)) {
			obj.dom.scrollTop = target
			console.log('有属性，页面滚动位置', obj.dom.scrollTop, obj.dom, target)
		} else {
			obj.dom.style[attr] = target + 'px'
			console.log('无属性，页面滚动位置', obj.dom.style)
		}
		return
	}
	if (isScroll.includes(attr)) {
		current = getScroll(obj.dom, attr)
	} else {
		current = parseInt(getStyle(obj.dom, attr))
	}

	if (!obj.diff) {
		obj.diff = target - current
	}

	let speed = obj.diff / (duration / 1000) / fps

	speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed)

	obj.timer = setTimeout(function () {
		var canStop = Math.abs(target - current) <= Math.abs(speed) || Math.abs(target - current) <= 0 || current + speed === obj.previous

		if (canStop) {
			clearTimeout(obj.timer)

			obj.timer = null
			obj.diff = null
			obj.previous = null
		}

		if (isScroll.includes(attr)) {
			obj.dom.scrollTop = obj.dom.scrollTop = current + speed
		} else {
			obj.dom.style[attr] = current + speed + 'px'
		}

		obj.previous = current + speed

		if (!canStop) {
			loopMapMove(obj, attr, target, duration)
		}
	}, interval)
}

export { checkEnv, dateFormStr, judgeVersion, shareHandle, loopMapMove, stopMapMove, checkUUID, spellParams,
    getCookie,
    addCookie,
    clearCookie,
    URISearch,
    registerHost }
