import { useState, useEffect, useContext } from "react";
import { PageContext } from '../../../context/context.js';
import prise01 from "../../../assets/images/prise_01.png";
import prise02 from "../../../assets/images/prise_02.png";
import prise03 from "../../../assets/images/prise_03.png";
import prise04 from "../../../assets/images/prise_04.png";
import fix01 from "../../../assets/images/fix01.png";
import fix02 from "../../../assets/images/fix02.png";


function Prize() {
  const { page, map, role } = useContext(PageContext);
  const [pageData, setPageData] = page
  const [mapData, setMapData] = map
  const [roleData, dispatch, rolReducer] = role
  switch (pageData.finalPrizeId) {
    case 234:
      return (
        <div
        className={`big_prize_item ${
          pageData.finalPrizeId != 0 ? "got_big_prize" : ""
        }`}
        >
          <img
            className="big_prize_item_img"
            src={prise01}
          />
          <div className="big_prize_item_text">72 元券包</div>
        </div>
      );
    case 235:
      return (
        <div
          className={`big_prize_item ${
            pageData.finalPrizeId != 0 ? "got_big_prize" : ""
          }`}
        >
          <img
            className="big_prize_item_img"
            src={prise03}
          />
          <div className="big_prize_item_text"> 出行免单 </div>
        </div>
      );
    case 236:
      return (
        <div
          className={`big_prize_item ${
            pageData.finalPrizeId != 0 ? "got_big_prize" : ""
          }`}
        >
          <img
            className="big_prize_item_img"
            src={prise02}
          />
          <div className="big_prize_item_text"> PLUS年卡 </div>
        </div>
      );
    case 237:
      return (
        <div
          className={`big_prize_item ${
            pageData.finalPrizeId != 0 ? "got_big_prize" : ""
          }`}
        >
          <img
            className="big_prize_item_img"
            src={prise04}
          />
          <div className="big_prize_item_text"> 2000 元 </div>
        </div>
      );
    default:
      return (
        <>
          <div
            className={`big_prize_item ${
              pageData.finalPrizeId != 0 && "got_big_prize"
            }`}
          >
            <img
              className="big_prize_item_img"
              src={prise01}
            />
            <div className="big_prize_item_text"> 72 元券包 </div>
          </div>
          <div
            className={`big_prize_item ${
              pageData.finalPrizeId != 0 && "got_big_prize"
            }`}
          >
            <img
              className="big_prize_item_img"
              src={prise02}
            />
            <div className="big_prize_item_text"> PLUS年卡 </div>
          </div>
          <div
            className={`big_prize_item ${
              pageData.finalPrizeId != 0 && "got_big_prize"
            }`}
          >
            <img
              className="big_prize_item_img"
              src={prise03}
            />
            <div className="big_prize_item_text"> 出行免单 </div>
          </div>
          <div
            className={`big_prize_item ${
              pageData.finalPrizeId != 0 && "got_big_prize"
            }`}
          >
            <img
              className="big_prize_item_img"
              src={prise04}
            />
            <div className="big_prize_item_text"> 2000 元 </div>
          </div>
        </>
      );
  }
}

function PlayFixed({ fixdata, onConditionChange }) {
  const { page, map, role } = useContext(PageContext);
  const [pageData, setPageData] = page
  const [mapData, setMapData] = map
  const [roleData, dispatch, rolReducer] = role
  return (
    <>
      <div className="big_prize_fixed" style={fixdata.fixTopStyle}>
        <div className="fixed_top_box1">
          <img className="fixed_top_01" src={fix01} />
          <div className="fixed_date"> {pageData.activeDateStr} </div>
        </div>
        <div>
          <img className="fixed_top_02" src={fix02} />
        </div>
        <div className="big_prize_list">
          <div className="fixed_top_guide">
            {pageData.finalPrizeId == 0
              ? "摇骰子通关抽取终极礼包"
              : "恭喜您抽中的终极奖品为"}
          </div>
          <Prize></Prize>
        </div>
      </div>
    </>
  );
}

export default PlayFixed;
