
import { useState, useEffect, useContext, createContext, useReducer } from 'react';
import { PageContext } from './context/context.js';
import Main from './pages/Main/main.js';

function App() {
  function useRoleData() {
    const rolReducer = (state, action) => {
      switch (action.type) {
        case 'set':
          return { ...state, ...action.payload }
        default:
          return state
      }
    }
    return [...useReducer(rolReducer, {}), rolReducer]
  }
  // function updataRole (action) {
  //   dispatch(action)
  // }

  const [pageData, setPageData] = useState({})
  const [mapData, setMageData] = useState({})
  const [roleData, dispatch, rolReducer] = useRoleData()

  
  
  // console.log('rolReducer', rolReducer)
  // useEffect(() => {
  //   updataRole({ type: 'set', payload: { name: '123' } })
  //   console.log(rolReducer(roleData, { type: 'set', payload: { name: '123' } }))
  // }, [])
  return (
    <PageContext.Provider value={{
      page: [pageData, setPageData],
      map: [mapData, setMageData],
      role: [roleData, dispatch, rolReducer]
    }}>
      <Main />
    </PageContext.Provider>
  );
}



export default App;
