import { useState, useEffect, useContext } from "react";
import { PageContext } from '../../../context/context.js';
import prise01 from "../../../assets/images/prise_01.png";
import prise02 from "../../../assets/images/prise_02.png";
import prise03 from "../../../assets/images/prise_03.png";
import prise04 from "../../../assets/images/prise_04.png";



function PrizeLoop() {
  const { page, map, role } = useContext(PageContext);
  const [pageData, setPageData] = page
  const [mapData, setMapData] = map
  const [roleData, dispatch, rolReducer] = role
  const { finalPrizeId, allStep, taskList, roadPrizes, Landmarks, activeDateStr} = pageData
  const [swiperSlide, setSwiperSlide] = useState({
    phone: '',
    name: '',
    type: ''
  })
  const [slideIndex, setSlideIndex] = useState(0)
  const [stopSwiperAni, setStopSwiperAni] = useState(true)
  const [timer, setTimer] = useState({
    stay: null,
    loop: null
  })

  function ImgIcon() {
    if (swiperSlide.type == 'coupons') {
      return <img className="award_broad_img" src={prise01} />
    }
    if (swiperSlide.type == 'plus') {
      return <img className="award_broad_img" src={prise02} />
    }
    if (swiperSlide.type == 'coupon') {
      return <img className="award_broad_img" src={prise03} />
    }
    if (swiperSlide.type == 'money') {
      return <img className="award_broad_img" src={prise04} />
    }
  }
  
  useEffect(() => {
    function swiperMove() {
      if (slideIndex >= pageData.prizeMsgList.length) {
        setSlideIndex(0)
      }
      const swiperSlide = pageData.prizeMsgList.find((pItem, pIndex) => {
        return pIndex == slideIndex
      })
      swiperSlide.phone = (''+swiperSlide.phone).substr(-4)
      setSlideIndex(slideIndex => slideIndex++)
      setSwiperSlide(swiperSlide)
    }
    function loopSwiper() {
      setStopSwiperAni(true)
      const stay = setTimeout(() => {
        setStopSwiperAni(false)
        swiperMove()
        const loop = setTimeout(() => {
            loopSwiper()
        }, 6600)
        setTimer({
          ...timer,
          loop: loop
        })
      }, 2000)
      setTimer({
        ...timer,
        stay: stay
      })
    }
    loopSwiper()
    return () => {
      clearTimeout(timer.stay)
      clearTimeout(timer.loop)
    }
  }, [swiperSlide])
  return (
    <div className={`award_broad_box ${!stopSwiperAni ? 'swiperAni' : ''}`}>
			<div className="award_broad_text1">尾号{swiperSlide.phone}</div>
			<div className="award_broad_text2">获得了{swiperSlide.name}</div>
			<ImgIcon></ImgIcon>
		</div>
  )
}

export default PrizeLoop;