import { useState, useEffect, useCallback, useRef } from "react"

function Loading ({onLoadEnd}) {
  const [progress, setProgress] = useState({
    show: true,
    width: 0
  })
  // const progressRef = useRef(progress)
  
  const progressLoad = function () {
    if (progress.width >= 100) {
      console.log("加载完毕~", progress.width)
      onLoadEnd()
    } else {
      // console.log("加载进度", progress.width)
      setProgress({
        ...progress,
        width: progress.width + 1
      })
    }
    
  }
  // const progressLoad2 = function () {
  //   if (progressRef.current.width >= 100) {
  //     console.log("加载完毕~", progressRef.current)
  //     onLoadEnd()
  //   } else {
  //     console.log("加载进度", progressRef.current)
  //     setProgress({
  //       ...progressRef.current,
  //       width: progressRef.current.width + 1
  //     })
  //   }
    
  // }

  useEffect(() => {
    // console.log("来了老弟", progress)
    const timer = setTimeout(() => {
      progressLoad();
      // console.log("加载进度", progress)
    }, 15)
    return () => {
      clearTimeout(timer)
    }
  }, [progress])

  // useEffect(() => {
  //   progressRef.current = progress
  // }, [progress])
  // useEffect(() => {
  //   console.log("来了老弟", progressRef.current)
  //   const timer = setInterval(() => {
  //     progressLoad2()
  //   }, 15)
  //   return () => {
  //     clearInterval(timer)
  //   }
  // }, [])
  
  return (
    <div className="load_box">
			<div>
				<div className="prog_bar">
					<div className="prog_bar_color" style={{width: progress.width + '%'}}></div>
				</div>
			</div>
		</div>
  )
}

export default Loading;