import axios from 'axios'
import qs from 'qs'
import { registerHost } from '../common.js'

console.log('vue运行环境------', process.env.NODE_ENV)
// axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? registerHost('https://gw-passenger-wap.01zhuanche.com') : registerHost('https://gw-passenger-wap.01zhuanche.com');
// axios.defaults.timeout = 3000
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// console.log('registerHost', registerHost('gw-passenger-wap.01zhuanche.com/activity'))

const instance = axios.create({
    // baseURL: process.env.NODE_ENV === 'production' ? registerHost('https://gw-passenger-wap.01zhuanche.com') : registerHost('https://gw-passenger-wap.01zhuanche.com'),
    baseURL: 'http://localhost:3000',
    timeout: 3000,
    withCredentials: true
})
instance.interceptors.request.use(function (config) {
    console.log('请求参数拦截', config.url)
    console.log(config)
    // if (config.method == 'get') {
    //     config.data = qs.stringify(config.data)
    // }
    return config
}, function (error) {
    return Promise.reject(error)
})


instance.interceptors.response.use(function (result) {
    console.log('请求结果拦截', result.config.url)
    console.log(result)
    return result.data
}, function (error) {
    return Promise.reject(error);
})
const request = {
    get (url, params, noBaseURL) {
        const config = {
            method: 'get',
            url: url,
            params: params || {}
        }
        if (noBaseURL) {
            config.baseURL = ''
        }
        return instance(config)
    },
    post (url, params) {
        const config = {
            method: 'post',
            url: url,
            data: params || {}
        }
        return instance(config)
    }
}

export default request
