import { useState, useEffect, useContext, createContext } from "react";
import Loading from "./components/loading.js";
import Play from "./components/play.js";
import "./main.scss";
import { PageContext } from "../../context/context.js";
import MapObj from "../../utils/mapData.js";
import {
  addCookie,
  getCookie,
  clearCookie,
  URISearch,
  registerHost,
  checkUUID,
  checkEnv,
  shareHandle,
  loopMapMove,
  stopMapMove,
  dateFormStr,
} from "../../utils/common.js";
import {
  getMapData,
  loadInfo,
  loadInfoAgain,
  activityConfig,
  drawPrize,
  drawBigPrize,
} from "../../utils/service/api.js";

function Main() {
  const [updataInfo, setUpdataInfo] = useState(0);
  const { page, map, role } = useContext(PageContext);
  const [pageData, setPageData] = page
  const [mapData, setMapData] = map
  const [roleData, dispatch, rolReducer] = role
  console.log("obj", page,map, role);
  console.log("data", pageData, mapData, roleData)
  const [loadEnd, setLoadEnd] = useState(false);
  const [fixMoudule, setFixMoudule] = useState({
    canShow: true,
    fixTopStyle: {
      transform: "translateY(0)",
      opacity: 1,
    },
  });
  const [touchStatus, setTouchStatus] = useState(true); // touchstart false,touchend true
  const [headTimer, setHeadTimer] = useState(null);
  const loadend = () => {
    setLoadEnd(true);
  };
  const refresh = () => {
    setUpdataInfo((prevUpdataInfo) => prevUpdataInfo + 1);
  };
  const changeCanShow = (status) => {
    setFixMoudule({
      ...fixMoudule,
      canShow: status,
    });
  };

  const hiddenFixTop = () => {
    if (loadEnd) {
      console.log("隐藏浮层");
      setFixMoudule({
        ...fixMoudule,
        fixTopStyle: {
          transform: "translateY(-110%)",
          opacity: 1,
        },
      });
      setTimeout(() => {
        setFixMoudule({
          ...fixMoudule,
          fixTopStyle: {
            transform: "translateY(-110%)",
            opacity: 0,
          },
        });
      }, 500);
    }
  };
  const showFixTop = () => {
    if (loadEnd && fixMoudule.canShow) {
      setTimeout(() => {
        console.log("显示浮层");
        setFixMoudule({
          ...fixMoudule,
          fixTopStyle: {
            transform: "translateY(0)",
            opacity: 1,
          },
        });
      }, 1500);
    }
  };
  useEffect(() => {
    async function loadGameMap() {
      const res = await getMapData();
      const serializer = new DOMParser();
      const serialized = serializer.parseFromString(res, "application/xml");
      console.log("serialized-----", serialized);
      const mapDom = serialized.querySelector("map");
      const width = mapDom.getAttribute("width");
      const height = mapDom.getAttribute("height");
      const tilewidth = mapDom.getAttribute("tilewidth");
      const tileheight = mapDom.getAttribute("tileheight");
      const dataStr = serialized.querySelector("data").innerHTML;
      console.log("data", dataStr);
      setMapData({
        width: width,
        height: height,
        tilewidth: tilewidth,
        tileheight: tileheight,
        tmxData: MapObj.formData(dataStr, width),
        vh: document.documentElement.clientHeight,
        vw: document.documentElement.clientWidth,
      });
      // setRoleData({
      //   start: null, // 起始位置
      //   end: null, // 结束位置
      //   current: null, // 当前位置
      //   previous: null, // 上一位置
      //   next: null, // 下一位置
      //   angle: 0, // 当前角度
      // });
      dispatch({
        type: 'set',
        payload: {
          start: null, // 起始位置
          end: null, // 结束位置
          current: null, // 当前位置
          previous: null, // 上一位置
          next: null, // 下一位置
          angle: 0, // 当前角度
        }
      })
    }
    loadGameMap();
  }, []);
  useEffect(() => {
    async function loadData() {
      const activeRes = (await activityConfig({ code: "dfw" })) || {};
      const activeResult = activeRes.data;
      const updataActiveData = {
        inAvtive:
          activeResult.time <
          new Date(activeResult.endTime.replace(/-/g, "/")).getTime(),
        activeDateStr: `活动日期：${dateFormStr(
          activeResult.startTime,
          "M-D"
        )}-${dateFormStr(activeResult.endTime, "M-D")}`,
      };
      const userInfoRes =
        (await loadInfo({
          sso_tk: 1,
        })) || {};
      const userInfoResult = userInfoRes.data;
      const updataUserInfo = {
        remainCount: userInfoResult.remainCount,
        allStep: userInfoResult.stepCount || 1,
        finalPrizeLock: userInfoResult.stepCount == 36,
        finalPrizeId: userInfoResult.bigPrizeId || 0,
        taskList: userInfoResult.taskCountList.map((item) => {
          return {
            ...item,
            lockText:
              item.code == 1
                ? "登录领"
                : item.code == 2
                ? "去分享"
                : item.code == 3
                ? "去打车"
                : item.code == 4
                ? "去开通"
                : item.code == 5
                ? "去完善"
                : "",
            unLockText: "已完成",
          };
        }),
        prizeMsgList: userInfoResult.prizeList.map((item) => {
          return {
            ...item,
            ...MapObj.allPrize.find((aItem) => {
              return aItem.prizeId == item.prizeId;
            }),
          };
        }),
        myPrize: userInfoResult.awardList.map((mItem) => {
          return {
            ...mItem,
            ...MapObj.allPrize.find((aItem) => {
              return aItem.prizeId == mItem.prizeId;
            }),
          };
        }),
        Landmarks: MapObj.Landmarks.map((lItem) => {
          return {
            ...lItem,
            ...MapObj.allPrize.find((aItem) => {
              return lItem.prizeId == aItem.prizeId;
            }),
            ...userInfoResult.awardList.find((aItem) => {
              return lItem.prizeId == aItem.prizeId;
            }),
          };
        }),
      };

      const arr = [];
      for (var i = updataUserInfo.myPrize.length - 1; i > -1; i--) {
        if (
          updataUserInfo.myPrize[i].prizeId >= 230 &&
          updataUserInfo.myPrize[i].prizeId <= 233
        ) {
          arr.push(updataUserInfo.myPrize[i]);
        }
      }
      updataUserInfo.roadPrizes = arr.map((rItem) => {
        return {
          ...rItem,
          coordinates: MapObj.pathCoordinate[rItem.stepCount - 1],
        };
      });
      setPageData((prevPageData) => {
        return {
          ...prevPageData,
          ...updataActiveData,
          ...updataUserInfo,
        };
      });
    }
    loadData();
  }, [updataInfo]);

  return (
    <div
      className="monopoly"
      onTouchStart={hiddenFixTop}
      onTouchEnd={showFixTop}
    >
      {loadEnd ? (
        <Play fixdata={fixMoudule} onConditionChange={changeCanShow} refresh={refresh} loadEnd=""></Play>
      ) : (
        <Loading onLoadEnd={loadend}></Loading>
      )}
    </div>
  );
}
export default Main;
